.basket-aside {
  padding: 1rem;

  &__item {

    @include media-breakpoint-up(lg) {
      padding-bottom: 1rem;
    }
    display: flex;
    border-bottom: 1px solid $mercury;
    margin-bottom: 1.5rem;
    padding-bottom: 0.875rem;

    &:last-child {
      margin-bottom: 1rem;
    }
  }

  &__image {

    @include media-breakpoint-up(lg) {
      width: 48px;
    }
    width: 96px;
    margin-right: 1rem;
  }

  &__body {
    flex: 1;
  }

  &__list {
    @include list-unstyled();
    margin-bottom: 0;
  }

  &__pieces {
    color: $gray-600;
    margin-left: 4px;
  }

  &__footer {
    border-bottom: 1px solid #e9e9e9;
    padding-bottom: 1.125rem;
    margin-bottom: 1.125rem;
  }

  &__total {
    font-size: 19px;
    text-align: right;
    margin-left: auto;
  }
}
