.radio-label {
  font-size: 14px;
  color: $gray-text;
  margin-bottom: 14px;
}

.form-radio-group {
  display: flex;
  flex-direction: column;
  row-gap: 19px;
}

.form-radio {
  display: flex;
  column-gap: 8px;
  cursor: pointer;
  margin-bottom: 0;

  input[type='radio'] {
    appearance: none;
    width: 24px;
    height: 24px;
    border: 1px solid $gray-text;
    border-radius: 50%;
    position: relative;
    background-color: transparent;

    &:checked::before {
      content: '';
      display: block;
      width: 20px;
      height: 20px;
      background-color: $gray-text;
      border-radius: 50%;
      margin-top: 1px;
      margin-left: 1px;
    }

    &:checked+span {
      font-weight: 500;
    }
  }

  span {
    font-size: 16px;
    white-space: nowrap;
  }
}