.stepper {

  @include media-breakpoint-up(lg) {
    margin-top: -5px;
  }
  display: flex;
  align-items: center;

  &__input {
    text-align: center;
    -moz-appearance: textfield;
    border-color: transparent;
    width: 24px;
    height: 30px;
    padding: 0;
  }

  &__btn {

    @include hover {
      color: $dark;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 24px;
    min-width: 24px;
    height: 30px;
    padding: 0;
    cursor: pointer;
    color: $gray-400;

    &:before {
      content: "";
      display: block;
      width: 10px;
      height: 1px;
      background-color: currentColor;
    }

    &_up {
      &:after {
        content: "";
        display: block;
        position: absolute;
        width: 1px;
        height: 10px;
        background-color: currentColor;
      }
    }
  }
}
