.form-color {
  display: flex;
  align-items: center;
  column-gap: 0.625rem;
  cursor: pointer;
  position: relative;
  margin-bottom: 0;

  input {
    display: none;
  }

  .swatch {
    width: 24px;
    height: 24px;
    border-radius: 4px;
    flex-shrink: 0;
  }

  .label-text {
    font-size: 16px;
    transition: all 0.2s ease;
    margin-top: 1px;
  }

  input:checked+.swatch+.label-text {
    font-weight: 500;
  }

  .form-color__icon {
    margin-left: auto;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.2s ease;
    pointer-events: none;
  }

  input:checked+.swatch+.label-text+.form-color__icon {
    opacity: 1;
    pointer-events: auto;
  }
}