.option {
  margin-bottom: 1.375rem;

  &__header {

    @include media-breakpoint-up(lg) {
      flex-direction: row;
      padding-top: 19px;
      padding-bottom: 17px;
    }
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 1rem 2rem;

    &:before {

      @include media-breakpoint-up(lg) {
        top: 50%;
        margin-top: -.25rem;
      }
      content: "";
      display: block;
      position: absolute;
      top: (1.625rem);
      left: (1rem - .125rem);
      width: .5rem;
      height: .5rem;
      border: 1px solid $dark;
      transition: background-color .2s ease-in-out;
      border-radius: 50%;
      background-color: transparentize($dark, 1);
    }

    &[aria-expanded="true"] {
      &:before {
        background-color: transparentize($dark, 0);
      }
    }
  }

  &__title {

    @include media-breakpoint-up(lg) {
      padding-right: 1rem;
    }
  }

  &__hint {
    @include media-breakpoint-up(lg) {
      margin-left: auto;
    }
  }

  &__body {
    padding: 0 32px;

    .form-group:last-child {
      margin-bottom: 1.625rem;
    }
  }
}
