.checkout {
  &__radio {
    .option__header[aria-expanded="true"] {
      pointer-events: none;
    }
  }
}

@keyframes city-spinner-border {
  to { transform: rotate(360deg); }
}

.spinner__city {
  position: relative;
  &:after {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -12px;
    right: 0;
    width: 24px;
    height: 24px;
    vertical-align: text-bottom;
    border: 1px solid currentColor;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-radius: 50%;
    animation: city-spinner-border .75s linear infinite;
  }
}
