.home {
  &__container {
    padding-top: 16px;

    @include media-breakpoint-up(md) {
      padding-top: 48px;
    }
  }

  .embla__dots {
    grid-template-columns: repeat(auto-fit, minmax(0, 32px));
    bottom: 43px;

    @include media-breakpoint-up(md) {
      bottom: 91px;
    }
  }

  .embla__slide-content {
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .banner, .banner-video, .embla__slide-double-images img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    .desktop {
      display: none;

      @include media-breakpoint-up(md) {
        display: block;
      }
    }

    .mobile {
      @include media-breakpoint-up(md) {
        display: none;
      }
    }

    .embla__slide-double-images {
      @include media-breakpoint-up(md) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        height: 100%;
      }
    }

    .slide-info {
      position: absolute;
      right: 0;
      bottom: 89px;
      left: 0;
      text-align: center;

      @include media-breakpoint-up(md) {
        bottom: 153px;
      }
    }

    .slide-uptitle {
      display: none;
      font-size: 16px;
      line-height: 19.2px;
      color: $white;
      margin-bottom: 8px;

      @include media-breakpoint-up(md) {
        display: block;
      }
    }

    .slide-title {
      font-size: 20px;
      line-height: 24px;
      letter-spacing: 0.2px;
      color: $white;
      text-transform: uppercase;

      @include media-breakpoint-up(md) {
        font-size: 28px;
        line-height: 33.6px;
        letter-spacing: 0.28px;
      }
    }

    .slide-btn {
      color: $white;
      padding: 0;
      font-size: 16px;
      line-height: 19.2px;
      margin-top: 14.5px;

      @include media-breakpoint-up(md) {
        margin-top: 12px;
      }

      @include hover {
        text-decoration: underline;
        
      }
    }
  }
}
