html {
  position: relative;
  height: 100%;
  font-size: 16px;

}
html, body {
  min-height: 100%;
}

body {
  font-size: 16px;

  @include media-breakpoint-up(lg) {
    font-size: 14px;
  }
}

@font-face {
  font-family: 'Basis Grotesque Pro';
  src: url('../fonts/BasisGrotesquePro-Regular.otf') format("opentype");
  src: url('../fonts/BasisGrotesquePro-Regular.woff2') format('woff2'),
  url('../fonts/BasisGrotesquePro-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Basis Grotesque Pro';
  src: url('../fonts/BasisGrotesquePro-Light.otf') format("opentype");
  src: url('../fonts/BasisGrotesquePro-Light.woff2') format('woff2'),
  url('../fonts/BasisGrotesquePro-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Basis Grotesque Pro';
  src: url('../fonts/BasisGrotesquePro-Bold.otf') format("opentype");
  src: url('../fonts/BasisGrotesquePro-Bold.woff2') format('woff2'),
  url('../fonts/BasisGrotesquePro-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Basis Grotesque Pro';
  src: url('../fonts/BasisGrotesquePro-Medium.otf') format("opentype");
  src: url('../fonts/BasisGrotesquePro-Medium.woff2') format('woff2'),
  url('../fonts/BasisGrotesquePro-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}

a {
  transition: color 0.2s ease-in-out;

  &:hover {
    text-decoration: none;
  }
}

.fieldset {
  margin-bottom: 1.75rem;

  &:last-of-type {
    margin-bottom: 1.25rem;

    @include media-breakpoint-up(lg) {
      margin-bottom: 0;
    }
  }
}
