.layout {
  display: flex;
  flex-direction: column;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;

  &__wrapper{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    position: relative;
    padding-top: 56px;
  
    @include media-breakpoint-up(md) {
      padding-top: 75px;
    }

    &.no-header-offset {
      padding-top: 0;
    }
  }

  &__navbar {
    padding: 0;
  }

  &__container {
    padding-top: 26px;
    padding-bottom: 20px;
  }
}

.container.home__container,
.container.catalog__container,
.container.product__container {
  width: 100%;
  max-width: 1920px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 16px;
  padding-left: 16px;

  @include media-breakpoint-up(md) {
    padding-right: 32px;
    padding-left: 32px;
  }

  @include media-breakpoint-up(lg) {
    padding-right: 48px;
    padding-left: 48px;
  }
}

.disable-scroll {
  overflow: hidden;
  touch-action: none;
  overscroll-behavior: contain;
}
