.product {
  &__nav {
    display: none;

    @include media-breakpoint-up(md) {
      display: block;
    }
  }

  &__body {
    @include media-breakpoint-up(md) {
      display: flex;
      align-items: flex-start;
      column-gap: 32px;
    }
  }

  &__slider-mobile {
    margin: 0 -16px;

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  &__slider-desktop {
    display: none;

    @include media-breakpoint-up(md) {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex: 1;
      column-gap: 32px;
    }

    .previews {
      width: 70px;
      position: sticky;
      top: 83px;
      max-height: calc(100svh - 83px);
      overflow-y: hidden;

      .preview-link {
        opacity: .5;
        transition: opacity ease-in-out .1s;

        &.is-active {
          opacity: 1;
          pointer-events: none;
        }
      }
    }

    .images {
      display: flex;
      flex-direction: column;
      flex: 1;

      img {
        max-width: none;
        width: 100%;
      }
    }
  }

  &__info {
    padding-top: 16px;
    display: flex;
    flex-direction: column;
    row-gap: 24px;

    @include media-breakpoint-up(md) {
      position: sticky;
      top: 83px;
      padding-top: 0;
      width: 30%;
      max-width: 450px;
      min-width: 300px;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 0;

    @include media-breakpoint-up(md) {
      line-height: 1.2;
    }
  }

  &__add-basket-mobile {
    position: sticky;
    bottom: 0;
    padding: 16px 0;
    background-color: #FFFFFF;

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  &__add-basket-desktop {
    display: none;

    @include media-breakpoint-up(md) {
      display: block;
      margin: 12px auto;
      height: 48px;
    }
  }

  &__price-wrapper {
    display: flex;
    column-gap: 12px;
    font-weight: 500;
    margin-top: 8px;
    line-height: 1.2;

    @include media-breakpoint-up(md) {
      font-size: 18px;
      line-height: 1.2;
    }

    .discount {
      text-decoration: line-through;
      color: $gray-text;
    }
  }

  &__desc {
    .title {
      font-size: 16px;
      line-height: 1.1;
      font-weight: 500;
      margin-bottom: 8px;
    }
  }

  &__accordions {
    margin-top: -10px;
    display: flex;
    flex-direction: column;
    row-gap: 14px;

    @include media-breakpoint-up(md) {
      margin-top: 6px;
    }
  }

  &__desc .body, &__accordions .body {
    color: $gray-text;
    font-size: 14px;
    line-height: 1.4;
    display: flex;
    flex-direction: column;
    row-gap: 8px;

    p {
      margin: 0;
    }
  }

  &__accordions .body, &__availability {
    @include media-breakpoint-up(md) {
      margin: 10px auto 16px;
    }
  }

  &__availability {
    font-size: 14px;
    line-height: 1.4;
    display: flex;
    flex-direction: column;
    row-gap: 8px;

    .grid {
      display: flex;
      justify-content: space-between;
      column-gap: 16px;
    }

    .address {
      color: $gray-text;
    }

    .sizes {
      display: flex;
      column-gap: 16px;
    }
  }

  &__related {
    margin-top: 64px;

    &.first {
      margin-top: 48px;
    }

    .title {
      font-size: 16px;
      line-height: 1.2;
      margin-bottom: 14px;
    }

    .slider {
      padding: 0 25px;
      margin: 0 -16px;
      overflow: hidden;
    }

    .embla {
      overflow: visible;

      &__container {
        .embla__slide {
          margin-right: 8px;
        }
      }
    }

    .grid__item__title {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .grid__item__title, .grid__item__prices {
      font-size: 12px !important;
      line-height: 1.2 !important;
      column-gap: 4px !important;
    }
  }
}

.color {
  &__title {
    font-size: 14px;
    line-height: 1.4;
    margin-top: 8px;
    color: $gray-text;
  }

  &__group {
    display: flex;
    column-gap: 6px;
  }

  &__btn {
    padding: 0;
    border-radius: 4px;
    overflow: hidden;
    width: 48px;
    height: 40px;
    position: relative;

    &:hover,
    &:checked,
    &.active {
      border: 0.5px solid $gray-text;
      padding: 2px;

      .color__value {
        border-radius: 2px;
      }
    }
  }

  &__value {
    width: 100%;
    height: 100%;
    display: block;
    background-color: currentColor;
  }
}

.dimension {
  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 14px;

    .title, .table-btn {
      font-size: 14px;
      line-height: 1.4;
      color: $gray-text;
    }

    .table-btn {
      padding: 0;
      border-width: 0;
      text-decoration: underline;

      @include hover-focus {
        text-decoration: none;
      }
    }
  }

  &__group {
    display: flex;
    flex-wrap: wrap;
    column-gap: 14px;
  }

  &__btn {
    padding: 0;
    white-space: nowrap;
    font-size: 16px;
    line-height: 40px;
    height: 40px;
    min-width: 40px;
    padding: 0 10px;

    &:hover {
      opacity: .6;
    }

    &.active {
      border-color: $gray-text;
      opacity: 1;
    }
  }
}

.measurement {
  &__btn {
    @include hover-focus {
      &:after {
        border-bottom-color: transparentize($dark, 0);
      }
    }
    display: inline-block;
    position: relative;
    padding: 0;
    border-width: 0;
    margin-bottom: 20px;

    &:after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -2px;
      left: 0;
      transition: border-bottom-color .2s ease-in-out;
      border-bottom: 1px solid transparentize($dark, .8);
    }
  }

  &__modal-dialog {
    @include media-breakpoint-up(lg) {
      max-width: 406px;
    }
  }

  &__modal-body {
    padding: 28px 22px 22px 22px;
    @include media-breakpoint-up(md) {
      padding-left: 48px;
      padding-bottom: 32px;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }

    &-title {
      margin-bottom: 6px;
      font-weight: bold;
    }
  }
}

@keyframes btn-spinner-border {
  to { transform: rotate(360deg); }
}

.btn__spinner {
  position: relative;
  &:after {
    content: "";
    margin-left: 6px;
    display: inline-block;
    position: absolute;
    top: 50%;
    margin-top: -7px;
    width: 14px;
    height: 14px;
    vertical-align: text-bottom;
    border: 1px solid currentColor;
    border-right-color: transparent;
    border-bottom-color: transparent;
    border-radius: 50%;
    animation: btn-spinner-border .75s linear infinite;
  }
}
