.product-subscribe {
  &__dialog {
    @include media-breakpoint-up(sm) {
      max-width: 448px;
    }
  }
  &__header {
    padding-top: 40px;
  }
  &__body {
    @include media-breakpoint-up(md) {
      padding: 10px 48px 56px 48px;
    }
  }
  &__title {
    font-size: 19px;
    text-align: center;
    font-weight: normal;
  }
  &__close {
    position: absolute;
    right: 20px;
    top: 20px;
  }
}
