.bottom-sheet {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  z-index: 1000;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &.open {
    visibility: visible;
    opacity: 1;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    transition: opacity 0.3s ease-in-out;
  }

  &__container {
    width: 100%;
    background: #fff;
    border-radius: 16px 16px 0 0;
    box-shadow: 0 -4px 10px rgba(0, 0, 0, 0.1);
    position: relative;
    transform: translateY(100%);
    transition: transform 0.3s ease-in-out;
  }

  &.open &__container {
    transform: translateY(0);
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 16px;

    h2 {
      font-size: 18px;
      font-weight: 400;
      margin: 0;
    }
  }

  &__close {
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: 40px;
    height: 40px;
  }

  &__close-icon {
    width: 24px;
    height: 24px;
  }

  &__content {
    padding: 16px 16px 0;
    max-height: calc(100vh - 112px);
    overflow-y: auto;
  }
}