.catalog {
  &__container {
    .nav-mobile {
      display: flex;
      justify-content: space-between;
      padding-top: 16px;
      padding-bottom: 24px;

      @include media-breakpoint-up(md) {
        display: none;
      }

      &__link {
        padding: 8px 0;
        border: none;
        font-size: 16px;
        line-height: 22.4px;

        span {
          margin-left: 12px;
          margin-top: 1px;
        }

        &.back {
          margin-left: -3px;
        }

        &__icon {
          min-width: 24px;
          height: 24px;
        }
      }
    }

    .nav-desktop {
      display: none;

      @include media-breakpoint-up(md) {
        display: block;
      }
    }
  }

  &__content {
    @include media-breakpoint-up(md) {
      display: flex;
      column-gap: 32px;
    }
  }

  &__sticky {
    position: sticky;
    top: 108px;
  }

  &__col {
    &.left {
      display: none;

      @include media-breakpoint-up(md) {
        display: block;
        min-width: 170px;
        max-width: 277px;
        width: 20%;
      }
    } 

    &.right {
      @include media-breakpoint-up(md) {
        flex: 1;
      }
    }
  }

  &__show-more {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
  }

  &__mobile-input-group {
    display: flex;
    flex-direction: column;
    row-gap: 19px;
    margin-top: 16px;
    margin-bottom: 6px;
    padding: 0 8px;
  }

  &__mobile-radio-group {
    padding: 0 8px;
  }

  &__mobile-filters {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
  }

  &__mobile-price-inputs {
    width: 100%;
    display: flex;
    align-items: center;
    column-gap: 8px;
    margin-top: 8px;

    label {
      display: none;
    }

    div {
      flex: 1;
      margin-bottom: 0;
    }

    .separator {
      width: 14px;
      height: 1px;
      background-color: $gray-text;
      flex: none;
    }
  }

  &__price-inputs {
    width: 280px;
    display: flex;
    align-items: center;
    column-gap: 8px;
    margin-bottom: 16px;

    label {
      display: none;
    }

    div {
      flex: 1;
      margin-bottom: 0;
    }

    .separator {
      width: 14px;
      height: 1px;
      background-color: $gray-text;
      flex: none;
    }
  }

  &__price-change-button {
    width: 100%;
  }

  &__mobile-buttons {
    position: sticky;
    bottom: 0;
    background-color: #fff;
    width: 100%;
    padding: 16px 0;
    display: flex;
    column-gap: 8px;

    button {
      flex: 1;
    }
  }
  
  &__desktop-filters {
    display: flex;;
    justify-content: space-between;
    width: 100%;
  }

  &__desktop-filters-wrapper {
    display: flex;
    column-gap: 24px;
  }

  &__desktop-form {
    display: none;
    margin-top: 2px;
    margin-bottom: 27px;

    @include media-breakpoint-up(md) {
      display: flex;
    }
  }

  &__filter-group {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
  }
}
