.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  background-color: #fff;
  z-index: 99;
  padding: 8px;
  transition: box-shadow 0.3s ease-in-out, background-color 0.2s ease-in-out;

  &--scrolled {
    box-shadow:
        0px 0px 5px -1px rgba(0, 0, 0, 0.05),
        0px 0px 25px -10px rgba(0, 0, 0, 0.15);
  }

  &.product {
    box-shadow: none;
    background-color: transparent;
  }

  @include media-breakpoint-up(md) {
    padding: 22px 48px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__column {
    width: 90px;

    @include media-breakpoint-up(md) {
      width: 150px;
    }

    &.right {
      display: inline-flex;
      justify-content: flex-end;
    }
  }

  &__logo {
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 5.4px;
    font-weight: 300;
    color: inherit;

    @include hover {
      color: inherit;
    }

    @include media-breakpoint-up(md) {
      font-size: 24px;
      line-height: 31px;
      letter-spacing: 7.44px;
    }
  }

  &_divider {
    border-bottom: 1px solid #e9e9e9;
  }

  &__search {
    position: absolute;
    width: 300px;
    right: 0;
  }
}

.header.transparent {
  box-shadow: none !important;
  background-color: transparent;
  color: white;

  .menu {
    &__link {
      transition: color 0.2s ease-in-out;
      @include hover {
        color: white;
        opacity: .8;
      }
    }
    &__basket {
      &-count {
        background-color: $white;
        color: $dark;
        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
      }
    }
  }
}

.menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: inline-flex;

  @include media-breakpoint-up(md) {
    column-gap: 24px;
    align-items: center;
  }

  &__lang {
    display: none;
    color: inherit;

    @include media-breakpoint-up(md) {
      display: block;
    }
  }

  &__link {
    width: 40px;
    height: 40px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: inherit;
    position: relative;

    @include media-breakpoint-up(md) {
      width: 24px;
      height: 24px;
    }
  }

  &__icon {
    color: inherit;
    min-width: 24px;
    height: 24px;
  }

  
  &__basket {
    position: relative;
    &-count {
      position: absolute;
      top: 12px;
      right: 7px;
      width: 16px;
      height: 16px;
      padding-top: 1px;
      background-color: $dark;
      color: $white;
      font-size: 12px;
      border-radius: 50%;
      text-align: center;
      line-height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;

      @include media-breakpoint-up(md) {
        top: 4px;
        right: -1px;
      }
    }
  }
}

