.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  position: relative;
}

[data-stroke="true"] {
  stroke: currentColor;
}
[data-fill="true"] {
  fill: currentColor;
}

.icon-arrow-back {
  width:(24/24)*1em;
}
.icon-arrow-down {
  width:(16/16)*1em;
}
.icon-arrow-left {
  width:(24/24)*1em;
}
.icon-arrow-right {
  width:(24/24)*1em;
}
.icon-basket {
  width:(24/24)*1em;
}
.icon-burger {
  width:(24/24)*1em;
}
.icon-close {
  width:(24/25)*1em;
}
.icon-close-small {
  width:(16/16)*1em;
}
.icon-filters {
  width:(24/24)*1em;
}
.icon-instagram {
  width:(14/14)*1em;
}
.icon-search {
  width:(24/24)*1em;
}
.icon-stop {
  width:(14/14)*1em;
}
.icon-whatsapp {
  width:(14/14)*1em;
}
