.cart {
  &__page-title {
    margin-bottom: 24px;
  }

  &__item {

    @include media-breakpoint-down(md) {
      flex-wrap: wrap;
      border: 1px solid transparentize($dark, 0.9);
    }
    border-top: 1px solid transparentize($dark, 0.9);
    padding-top: 1rem;
    display: flex;
    align-items: flex-start;

    &:not(:first-child) {
      margin-top: 1rem;
    }
  }

  &__image {

    @include media-breakpoint-up(lg) {
      width: 128px;
      margin-right: 2rem;
      margin-left: 0;
    }
    display: block;
    width: 96px;
    margin-right: 1rem;
    margin-left: 1rem;
  }

  &__body {

    @include media-breakpoint-down(md) {
      padding-right: 1rem;
    }
    flex: 1;
  }

  &__list {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
  }

  &__remove {
    color: $star-dust;
  }

  &__price {
    width: 140px;
    text-align: right;
  }

  &__actions {
    @include media-breakpoint-down(md) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 10px 1rem 10px 10px;
      border-top: 1px solid transparentize($dark, .9);
      margin-top: 1rem;
    }
  }
}
