.checkbox-label {
  font-size: 14px;
  color: $gray-text;
  margin-bottom: 14px;
}

.form-checkbox-group {
  display: flex;
  flex-direction: column;
  row-gap: 19px;
}

.form-checkbox {
  display: flex;
  column-gap: 8px;
  cursor: pointer;
  margin-bottom: 0;

  input[type='checkbox'] {
    appearance: none;
    width: 24px;
    height: 24px;
    border: 1px solid $gray-text;
    background-color: transparent;
    position: relative;
    cursor: pointer;

    &:checked::before {
      content: '';
      display: block;
      width: 20px;
      height: 20px;
      background-color: $gray-text;
      margin-top: 1px;
      margin-left: 1px;
    }

    &:checked+span {
      font-weight: 500;
    }
  }

  span {
    font-size: 16px;
  }
}

.form-checkbox-desktop {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  cursor: pointer;
  position: relative;
  margin-bottom: 0;

  input[type='checkbox'] {
    display: none;
  }

  &__label {
    flex: 1;
  }

  .icon {
    flex-shrink: 0;
    margin-left: 10px;
    min-width: 16px;
    height: 16px;
    display: none;
  }

  input[type='checkbox']:checked+.form-checkbox-desktop__label {
    font-weight: 500;
  }

  input[type='checkbox']:checked+.form-checkbox-desktop__label+.icon {
    display: block;
  }
}