.list__header {
  font-size: 19px;
  margin-bottom: 10px;
}

.list__disc {
  list-style: none;
  padding-left: 0;
  line-height: (22px / 14px);
  margin-bottom: 16px;

  > li {
    padding-left: 12px;
    &:before {
      content: "•";
      padding-right: 8px;
      margin-left: -12px;
    }
  }
}
