.nav-aside {
  padding-top: 3px;

  &__item {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  &__link {
    position: relative;
    &:after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -2px;
      left: 0;
      transition: border-bottom-color .2s ease-in-out;
      border-bottom: 1px solid transparentize($dark, .8);
      pointer-events: none;
    }

    &.active {
      font-weight: bold;
      &:after {
        border-bottom-color: transparentize($dark, 1);
      }
    }

    &:hover {
      color: $dark;

      &:after {
        border-bottom-color: transparentize($dark, 0);
      }
    }
  }
}
