.thank-order {
  &__container {
    padding-top: 24px;
    padding-bottom: 50px;
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__title {
    font-size: 25px;
    margin-bottom: 16px;
