.m-basket {
  &__dropdown {
    max-width: 388px;
    width: calc(100vw - 16px);
    padding: 12px 20px 20px;
    box-shadow: 0 40px 20px rgba(0, 0, 0, 0.29);
    background-color: white;
    position: absolute;
    top: 74px;
    right: 36px;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, .15);
    visibility: hidden;
    opacity: 0;
    transition: opacity .3s, visibility .3s;
    pointer-events: none;

    &.show {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
    }

    &:after {
      content: "";
      display: block;
      position: absolute;
      top: -7px;
      right: 1rem;
      width: 14px;
      height: 14px;
      border: 1px solid $dropdown-border-color;
      transform: rotate(45deg);
      border-bottom-color: transparent;
      border-right-color: transparent;
      background-color: $dropdown-bg;
    }
  }

  &__title {
    font-size: 19px;
    margin-bottom: 16px;
    color: #212121;
  }

  &__item {
    display: flex;
    margin-bottom: 1.5rem;
    position: relative;

    &:not(:last-child) {
      @include media-breakpoint-up(lg) {
        padding-bottom: 1rem;
      }
      border-bottom: 1px solid $mercury;
      padding-bottom: 0.875rem;
    }

    &:last-child {
      margin-bottom: 1rem;
    }
  }

  &__image {
    width: 48px;
    margin-right: 1rem;
  }

  &__body {
    flex: 1;
    padding-right: 1rem;
  }

  &__remove {
    position: absolute;
    top: -2px;
    right: 0;
    padding: 5px;
    font-size: 9px;
  }
}

