.dropdown {
  position: relative;

  &__header {
    cursor: pointer;
    display: flex;
    align-items: center;
    font-size: 18px;
  }

  &__icon {
    margin-left: 8px;
    min-width: 16px;
    width: 16px;
    height: 16px;
  }

  &.open .dropdown__title {
    font-weight: 500;
    text-decoration: underline;
  }

  &.open &__icon {
    transform: rotate(180deg);
  }

  .dropdown__counter {
    background-color: $gray-text;
    color: white;
    font-size: 12px;
    border-radius: 999px;
    margin-left: 8px;
    line-height: 17px;
    min-width: 16px;
    height: 16px;
    text-align: center;
    display: inline-block;
    text-decoration: none !important;

    &.hidden {
      display: none;
    }
  }

  &__content {
    display: none;
    position: absolute;
    top: 125%;
    z-index: 10;
    background: #fff;
    min-width: 246px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.05);
    padding: 16px;
  }

  &--left &__content {
    left: -16px;
  }

  &--right &__content {
    right: -16px;
  }

  &.open &__content {
    display: block;
  }
}