.footer {
  padding: 16px 0;
  border-top: 1px solid #989690;
  margin-top: 64px;

  @include media-breakpoint-up(md) {
    padding: 32px 0 48px;
  }

  @include media-breakpoint-up(lg) {
    margin-top: 94px;
  }

  .content-container {
    display: flex;
    row-gap: 24px;
    flex-direction: column;
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 16px;
    padding-left: 16px;

    @include media-breakpoint-up(md) {
      display: flex;
      flex-direction: row-reverse;
      align-items: baseline;
      gap: 32px;
      padding-right: 32px;
      padding-left: 32px;
    }

    @include media-breakpoint-up(lg) {
      padding-right: 48px;
      padding-left: 48px;
    }
  }

  &__col {
    @include media-breakpoint-up(md) {
      width: 25%;
    }
  }

  .nav {
    @include media-breakpoint-up(md) {
      row-gap: 8px;
    }

    .nav-item {
      @include media-breakpoint-up(md) {
        display: flex;
      }
    }
  }

  &__logo {
    display: none;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 5.58px;
    font-weight: 300;

    @include media-breakpoint-up(md) {
      display: inline-block;
    }
  }

  &__title {
    font-size: 18px;
    line-height: 21.6px;
    margin: 0;
    margin-bottom: 16px;

    @include media-breakpoint-up(md) {
      font-size: 16px;
      line-height: 19.2px;
    }
  }

  &__link {
    font-size: 16px;
    line-height: 19.2px;
    padding: 6px 0;
    display: inline-block;

    @include media-breakpoint-up(md) {
      font-size: 14px;
      line-height: 16.8px;
      padding: 0;
      display: flex;
    }

    &.active {
      font-weight: 500;
      text-decoration: underline;
    }
  }

  &__address {
    display: none;

    @include media-breakpoint-up(md) {
      display: block;
    }
  }

  &__subscribe {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include media-breakpoint-up(md) {
      flex-direction: column-reverse;
      gap: 52px;
    }

    .email {
      .btn {
        width: 100%;
      }
    }

    .email__text {
      font-size: 16px;
      line-height: 22.4px;
      margin-bottom: 16px;

      @include media-breakpoint-up(md) {
        font-size: 14px;
        line-height: 19.6px;
      }
    }
  }

  &__mobile-langs {
    column-gap: 24px;

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  .location {
    margin-top: 16px;

    .info {
      margin: 0;
      margin-top: 8px;
      display: flex;
      row-gap: 8px;
      flex-direction: column;
      font-size: 14px;
      line-height: 16.8px;
      color: #989690;

      a {
        color: #989690;
      }

      * {
        display: flex;
      }
    }
  }
}
