.embla {
  overflow: hidden;
  position: relative;
}

.embla__container {
  display: flex;
}

.embla__slide {
  flex: 0 0 100%;
  min-width: 0;

  .embla[data-slides-to-show="2"] & {
    flex: 0 0 50%;
  }

  .embla[data-slides-to-show="3"] & {
    flex: 0 0 33.3333%;
  }
}

.embla__arrow {
  position: absolute;
  top: 0;
  bottom: 0;
  color: $white;
  padding: 0 16px;
  transition: opacity ease-in-out .3s;

  @include media-breakpoint-up(md) {
    padding: 0 48px;
  }

  @include hover {
    color: $white;
    opacity: .5;
  }

  &.prev {
    left: 0;
  }

  &.next {
    right: 0;
  }

  .icon {
    width: 24px;
    height: 24px;
  }
}

.embla__dots {
  position: absolute;
  right: 16px;
  bottom: 11px;
  left: 16px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
  justify-content: center;

  button {
    appearance: none;
    outline: none;
    cursor: pointer;
    border: none;
    background-color: transparent;
    padding: 0;
    width: 100%;
    height: 13px;
    
    .button-body {
      width: 100%;
      height: 2px;
      transition: background-color ease-in-out .2s;
      background-color: rgba(255, 255, 255, 0.28);
    }

    @include hover {
      .button-body {
        background-color: rgba(255, 255, 255, 0.6);
      }
    }

    &.is-selected {
      .button-body {
        background-color: $white;
      }
    }
  }
}