.grid {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(1, 1fr); 
  
  @media (min-width: 375px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include media-breakpoint-up(md) {
    gap: 24px;
    grid-template-columns: repeat(3, 1fr);
  }

  @include media-breakpoint-up(lg) {
    gap: 32px;
    grid-template-columns: repeat(4, 1fr);
  }

  &__item {
    .grid__item__image {
      aspect-ratio: 2 / 3;
      width: 100%;
      object-fit: cover;
      object-position: center;
      background-color: $gray-100;
    }

    .grid__item__title,
    .grid__item__prices {
      font-size: 14px;
      line-height: 16.8px;
      letter-spacing: -0.14px;

      @include media-breakpoint-up(md) {
        font-size: 16px;
        line-height: 19.2px;
        letter-spacing: -0.16px;
      }
    }

    .grid__item__title {
      margin-top: 8px;
      margin-bottom: 4px;

      @include media-breakpoint-up(md) {
        margin-top: 16px;
        margin-bottom: 8px;
      }
    }

    .grid__item__prices {
      display: flex;
      column-gap: 12px;
      font-weight: 500;
    }

    .grid__item__discount {
      text-decoration: line-through;
      color: $gray-500;
    }
  }
}

.load-more {
  display: flex;
  margin: 16px auto 0;
  text-decoration: underline;
  border: 0;
  padding: 12px 15px;
  font-size: 16px;

  @include hover {
    text-decoration: none;
  }

  @include media-breakpoint-up(md) {
    margin-top: 32px;
  }
}