.nav-trigger {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &.open {
    visibility: visible;
    opacity: 1;

    .nav-trigger__body {
      transform: translateX(0);
    }
  
    .nav-trigger__overlay {
      @include media-breakpoint-up(md) {
        display: block;
        z-index: 9;
        cursor: pointer;
      }
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    // position: fixed;
    // z-index: 10;
    // top: 0;
    // left: 0;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    transform: translateX(-100%);
    transition: transform .3s;
    background-color: $white;
    -webkit-overflow-scrolling: touch;

    @include media-breakpoint-up(md) {
      width: 320px;
      box-shadow:
          0px 0px 5px -1px rgba(0, 0, 0, 0.05),
          0px 0px 25px -10px rgba(0, 0, 0, 0.15);
    }

    .nav-trigger__close {
      padding: 0;
      width: 40px;
      height: 40px;

      @include media-breakpoint-up(md) {
        width: 24px;
        height: 24px;
        border: 0;
      }
    }

    .nav-trigger__icon {
      min-width: 24px;
      height: 24px;
    }
  }

  &__overlay {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: calc(100% - 320px);
  }

  &__header {
    padding: 8px;

    @include media-breakpoint-up(md) {
      padding: 23px 48px;
    }
  }

  &__footer {
    padding: 16px 16px 32px;

    @include media-breakpoint-up(md) {
      display: none;
    }

    button {
      width: 100%;
    }
  }

  &__scroll {
    height: 100%;
    overflow-y: scroll;
    padding: 8px 16px 16px;

    @include media-breakpoint-up(md) {
      padding: 23px 40px 48px 48px;
    }
  }
}

.nav-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  row-gap: 16px;

  &__link-container {
    position: relative;
    padding-right: 40px;

    &[role="button"] {
      transition: color 0.2s ease-in-out;
      cursor: pointer;

      @include hover {
        color: #585856;
      }
    }

    &.active {
      .nav-menu__icon {
        transform: rotate(180deg);
      }

      .nav-menu__link {
        font-weight: 500;
      }
    }
  }

  &__icon {
    min-width: 16px;
    height: 16px;
    position: absolute;
    top: 13px;
    right: 8px;
    transition: transform ease-in-out .3s;

    @include media-breakpoint-up(md) {
      top: 8px;
    }
  }

  &__link {
    display: block;
    font-size: 16px;
    line-height: 19.2px;
    padding: 10.4px 0;

    @include media-breakpoint-up(md) {
      padding: 6.4px 0;
    }

    &.featured {
      color: #FF0000;
    }

    &.selected {
      font-weight: 500;
    }

    &.first-level {
      font-size: 18px;
      line-height: 21.6px;
      padding: 9.2px 0;

      @include media-breakpoint-up(md) {
        padding: 5.2px 0;
      }
    }    
  }

  &__submenu-list {
    list-style: none;
    display: flex;
    flex-direction: column;
    row-gap: 4px;
    padding: 0 0 0 16px;
    height: 0;
    overflow: hidden;

    @include media-breakpoint-up(md) {
      padding-left: 24px;
    }
  
    &.second-level {
      padding-left: 0;
    }
  
    &.active {
      height: auto;
    }

    &>li:first-child {
      margin-top: 4px;

      @include media-breakpoint-up(md) {
        margin-top: 8px;
      }
    }

    &>li:last-child {
      margin-bottom: 8px;

      @include media-breakpoint-up(md) {
        margin-bottom: 16px;
      }
    }
  }
}