.accordion {
  position: relative;

  &__trigger {
    display: flex;
    align-items: center;
    column-gap: 16px;
    padding: 0;
    width: 100%;
    height: 40px;
    text-decoration: none;
    border: none;

    @include hover {
      text-decoration: none;
    }

    @include media-breakpoint-up(md) {
      height: auto;
    }

    &:focus {
      text-decoration: none;
    }
  }

  &__title {
    font-size: 16px;
    line-height: 19.2px;
    flex: 1;
    text-align: left;
  }

  &__icon {
    width: 16px;
    height: 16px;
  }

  .accordion__icon {
    transition: transform 0.3s ease-in-out;
  }

  &__content {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: max-height 0.3s ease-out, opacity 0.3s ease;
  }

  &.active {
    .accordion__content {
      opacity: 1;
    }

    .accordion__icon {
      transform: rotate(180deg);
    }
  }
}